import React from "react";
import OfferDeviceFilterMobileComponent from "eshop/modules/simfree/components/filter/OfferDeviceFilterMobileComponent"
import {ConditionalInfoComponent} from "../../../../../components/InfoComponent";
import MarketContextCheckboxView from "../../../../configurator/components/MarketContextCheckboxView";
import SalesChannel from "../../../../core/enum/SalesChannel";
import OfferType from "../../../../core/constants/OfferTypeEnum";
import SoftBundleGroup from "../../../../core/enum/SoftBundleGroup";

const ProductTabHeaderView = props => {
    const renderConditionalInfo = ({channels, selectedOfferType, softBundleGroup, hasLove}) => {
        if (channels.sales === SalesChannel.WWW &&
            selectedOfferType === OfferType.VOICE_LDF &&
            softBundleGroup === SoftBundleGroup.LDF) {
            return (
                <div className={"l-page-row u-padding u-padding-left-l"}>
                    <ConditionalInfoComponent
                        condition={hasLove}
                        type={"blueInfo"}
                        color={"black"}
                        bgColor={"white"}
                        title={"Pamiętaj!"}
                        padding={"noPadding"}
                        text={"Jesteś w Love dla Firm - dzięki temu korzystasz ze specjalnych rabatów. Jeśli chcesz korzystać z nich dalej - musisz mieć aktywną umowę na internet dla biura."}
                        altText={"Love dla firm to pakiet obejmujący abonament komórkowy i internet stacjonarny w Orange dzięki czemu koszt pierwszej karty jest od 35,00 zł + VAT, a drugą kartę dostaniesz za 0 zł."}>
                    </ConditionalInfoComponent>
                </div>
            );
        }
    };
    return (
        <div>
            <div className="l-page-row u-padding-top-l u-small-padding-top-l u-small-padding-l ">
                <div className="l-row">
                    <div className="l-col l-col-8 l-col-medium-12 l-col-small-12">
                        <h2 className="h1 u-no-spacing">{props.tabHeaderText}</h2>
                    </div>
                    <div className="l-col l-col-4 l-col-medium-12 l-col-small-12 u-right u-padding-top-s">
                        <MarketContextCheckboxView/>
                    </div>
                </div>
            </div>
            <div className="l-page-row u-padding u-padding-left-l">
                <div className="opl-tabs opl-tabs--secondary">
                    {props.options.length >= 1 && !props.isDuet? <div className="opl-tabs__nav-wrapper">
                        <div className="opl-tabs__nav-wrapper-inner">
                            <ul className="opl-tabs__nav" data-js-module="common/modules/opl-tabs"
                                data-js-options='{"tabNavLinkInnerCor": 30}'>
                                {
                                    props.options.map((option, index) =>
                                        <li className="opl-tabs__nav-item">
                                            <div
                                                className={"opl-tabs__nav-item__content"}>
                                                <a href="#" onClick={() => props.onChange(option.value)}
                                                   className={"opl-tabs__nav-link" + ((option.description === props.headerText) ? " is-active" : "")}
                                                   data-selected={(option.value == props.selectedOfferType ? "1" : "0")}
                                                   aria-selected={(option.value == props.selectedOfferType)}>
                                                    <span
                                                        className="opl-tabs__nav-link-inner"><span>{option.description || option.value}</span></span></a>
                                            </div>
                                        </li>
                                    )
                                }
                            </ul>
                        </div>
                    </div> : null}
                </div>
            </div>
            {
                renderConditionalInfo(props)
            }
            <OfferDeviceFilterMobileComponent descriptions={props.descriptions} channels={props.channels}/>
        </div>
    );
};

export default ProductTabHeaderView;